import {mapState,mapMutations} from 'vuex';
import uniNumberBox from '@/components/uni-number-box.vue';
import uniPopup from '@/components/uni-popup.vue';
export default {
	components: {
		uniNumberBox,
		uniPopup
	},
	data() {
		return {
			showCollapse:false,
			orderId:'',
			orderSn:'',
			payAmount:'',
			showPopupMiddle:false,
			isFocus:false,
			realInputs:[],
			password:'',
			ticketMoney:0,
			minExtraPrice:'',
			maxExtraPrice:'',
			type:'' //是余额支付还是消费券支付1--余额 2--消费券
		}
	},
	computed: {
		...mapState(['token']),
		extraMoney(){
			return this.payAmount - this.ticketMoney
		}
	},
	onShow(){
		// this.getShopcar();
	},
	onLoad(e) {
		console.log(e);
		if(e['orderId']){
			this.orderId = e['orderId'];
			this.orderSn = e['orderSn'];
			this.payAmount = e['payAmount'];
		}
		// this.reckon(this.payAmount);
		
	},
	onUnload(){
		if(this.loadType == 'place'){
			uni.switchTab({
				url:'/pages/tabBar/index/index'
			})
		}
	},
	onHide(){
		
	},
	methods: {
		reckon(allPrice){
			uni.request({
				url: this.websiteUrl + 'user/is_first_time',
				method: 'POST',
				data:{
					type:1
				},
				dataType:'json',//服务器返回json格式数据
				header:{'Content-Type':'application/json','Token':this.token},
				success: res => {
					var res = res.data;
					if(res.code == 200){
						console.log("是否为第一次消费券支付："+JSON.stringify(res.data));
						let isFirstTime = res.data;
						let recTicket;
						if(isFirstTime == 1){ //0代表第一次消费劵消费，则可以全部用消费券支付
							recTicket = parseInt(allPrice)
						}else { //1代表不是第一次消费券消费，最多只能用90%的消费券
							recTicket = parseInt(allPrice*0.9);
						}
						this.minExtraPrice = this.payAmount - recTicket;
						this.ticketMoney = recTicket;
					}else {
						// this.loadAddressList();
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		onNumberChange(value){
			console.log(value);
			this.ticketMoney = this.payAmount - value;
		},
		showCollapseBox(){
			this.showCollapse = !this.showCollapse;
			this.maxExtraPrice = this.payAmount;
			this.reckon(this.payAmount);
		},
		showPayPopup(type){
			this.type = type;
			if(type == 1){ //余额支付
				this.extraMoney = this.payAmount;
				this.ticketMoney = 0;
			}else { //消费券支付
				// this.extraMoney = this.payAmount
			}
			this.showPopupMiddle = true;
			this.isFocus = true;
		},
		hidePopup(){
			this.showPopupMiddle = false;
			this.isFocus = false;
			this.reckon(this.payAmount);
			this.realInputs = [];
		},
		realInput(e){
			console.log(e)
			let value = e.detail.value;
			let len = value.length;
			let that = this;
			if(/^[0-9]*$/g.test(value)) {
				this.password = value;
				this.realInputs = [];
				for(let i in this.password){
					that.realInputs[i] = that.password[i];
				}
				if(len === 6){
					console.log('开始支付了')
					this.getPay();
				}
			}
		},
		getPay(){
			let type = this.type;
			let money;
			if(type == 1){//money传订单金额
				money = this.payAmount;
			}else {
				money = this.ticketMoney;
			}
			let data = {
				orderId:this.orderId,
				payType:type,
				money:money,
				secPwd:this.password
			}
			console.log(data)
			uni.request({
				url: this.websiteUrl + 'shop/order_pay',
				method: 'POST',
				data:data,
				dataType:'json',//服务器返回json格式数据
				header:{'Content-Type':'application/json','Token':this.token},
				success: res => {
					var res = res.data;
					if(res.code == 200){
						console.log("支付成功："+JSON.stringify(res.data));
						uni.showToast({
							icon: 'success',
							title: '支付成功',
							duration:2000,
							success:function(){
								setTimeout(()=>{
									uni.navigateBack({
										delta:1
									})
								},2000)
							}
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		loadAddressList(){ //获取用户地址列表
			uni.request({
				url: this.websiteUrl + '/user/address_list',
				method: 'POST',
				data:{},
				dataType:'json',//服务器返回json格式数据
				header:{'Content-Type':'application/json','Token':this.token},
				success: res => {
					var res = res.data;
					if(res.code == 200){
						console.log("获取地址列表成功："+JSON.stringify(res.data));
						let addressId = res.data.list[0].id;
						this.addressId = addressId;
						this.getFirstAddress(addressId);
					}
				},
				fail: () => {},
				complete: () => {}
			});
		}
		
	}
}
